.quote {
  padding-left: 12px;
  color: rgb(157, 157, 157);
  border-left: 3px solid rgb(202, 202, 202);
}
.btn {
}
.btn-sm {
  display: inline-block;
  height: 20px;
  line-height: 20px;
  padding: 0 6px;
  border: 1px solid rgb(112, 193, 223);
  border-radius: 3px;
  font-size: 12px;
  cursor: pointer;
}
.btn-sm:hover {
  color: #fff;
  background-color: rgb(164, 186, 238);
}
.link {
  color: rgb(77, 140, 218);
  cursor: pointer;
}
.link:hover {
  text-decoration: underline;
}



